import React, { Component } from 'react';
import { render } from 'react-dom';
import DatePicker from './js/date-picker';

import './styles.css';

const renderDatePickerExample = (title, date, seperator, format, input, monthPicker, color, iconURL, iconPosition, selectorStyle, placeHolder, disabled) => {
  const style = {
    backgroundColor: 'gray',
    color: 'white',
    fontWeight: 600
  };

  return (
    <div className="example-wrapper">
      <div className="example-body">
        <div className="example-header">
          {title}
        </div>
        <div className="example-content">
          <div>{`<DatePicker`}</div>
          <div className="code-param">{`onDateSelect={() => { }}`}</div>
          {date && <div className="code-param">{`date="${date}"`}</div>}
          {input && <div className="code-param">{`input={true}`}</div>}
          {monthPicker && <div className="code-param">{`monthSelector={true}`}</div>}
          {format && <div className="code-param">{`dateFormat="${format}"`}</div>}
          {seperator && <div className="code-param">{`seperator="-"`}</div>}
          {color && <div className="code-param">{`color="#32A852"`}</div>}
          {iconURL && <div className="code-param">{`iconURL="assets/calendar.svg"`}</div>}
          {iconPosition && <div className="code-param">{`iconPosition="left"`}</div>}
          {selectorStyle && <div className="code-param">{`selectorStyle={{`}</div>}
          {selectorStyle && <div className="css-param">{`backgroundColor: 'gray'`}</div>}
          {selectorStyle && <div className="css-param">{`color: 'white'`}</div>}
          {selectorStyle && <div className="css-param">{`fontWeight: 600`}</div>}
          {selectorStyle && <div className="code-param">{`}}`}</div>}
          {placeHolder && <div className="code-param">{`placeHolder=${placeHolder }`}</div>}
          {disabled && <div className="code-param">{`disabled=${disabled}`}</div>}

          <div>{`/>`}</div>
        </div>
      </div>
      <div className="dp-example-wrapper">
        <DatePicker
          input={input}
          date={date ? date : null}
          dateFormat={format ? format : "DDMMYYYY"}
          placeHolder={placeHolder ? placeHolder : ''}
          monthSelector={monthPicker}
          seperator={seperator ? "-" : "/"}
          color={color ? color : '#1CA6D9'}
          iconURL={iconURL ? "assets/calendar.svg" : ''}
          iconPosition={iconPosition ? "left" : "right"}
          selectorStyle={selectorStyle ? style : {}}
          disabled={disabled}
        />
      </div>
    </div>
  )
}



